@font-face {
    font-family: "light";
    src: url("../fonts/FuturaPTLight.otf");
}
@font-face {
    font-family: "reg";
    src: url("../fonts/FuturaPTBook.otf");
}
@font-face {
    font-family: "md";
    src: url("../fonts/FuturaPTMedium.otf")
}
@font-face {
    font-family: "s-bold";
