﻿html{
    &::-webkit-scrollbar {
        width: 8px;
    }
    &::-webkit-scrollbar-track {
        background-color: #eee;
    }
    &::-webkit-scrollbar-thumb {
        // border-radius: 10px;
        // background-color: #000;
        background-image: linear-gradient(0deg, #f2dc91 0%, rgba(0,0,0,1) 60%);
    }

    scroll-behavior: smooth;

    overflow-x: hidden;

    scrollbar-width: thin;
    scrollbar-color: rgba(#eee,1);
    // scroll-behavior: smooth;
}

body{
    font-family: 'reg';
    overflow-y: hidden;
}



