/*
=====
LODEARS
=====
*/
html, body {
    // overflow-y: hidden;
    overflow-x: hidden;
}

ul {
  margin: 0;
  padding: 0;
}
a {
    text-decoration: none;
    font-family: inherit;
    font-size: inherit;
    color: inherit;
    &:hover, &:focus {
        text-decoration: none;
        color: inherit;
    }
}
button {
  border: none;
  outline: none;
  background-color: transparent;
  padding: 0;
}


.pre-loader {
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 9999;
    background-color: #000;
    display: flex;
    justify-content: center;
    align-items: center;

    .content {
      // position: relative;
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      width: 110px;
      height: 110px;
      padding: 2px;
      border-top: 2px solid #f2dc91;
      border-left: 2px solid #f2dc91;
      border-right: 2px solid #f2dc91;
      border-bottom: 2px solid #000;
      border-radius: 50%;
      animation: spin 2s linear infinite;
    }

    img {
        // animation: fade-scale-up-down 1.1s infinite alternate;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
    }

    @keyframes spin {
      0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
      }
      100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
      }
    }
    
}






  

.container {
  width: 1240px;
}

.owl-carousel .owl-item img {
    width: auto;
}







.search-container {
  display: none;
  position: fixed;
  // top: 0;
  // left: 0;
  z-index: 9999;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.9);
  .close {
    opacity: 0.9;
    width: 20px;
    height: 20px;
    // border-radius: 50%;
    background-color: #f2dc91;
    color: #000;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 35%;
    left: 50%;
    transform: translate(-50%, -50%);
    transition: all .3s ease-in-out;
    clip-path: polygon(20% 0%, 0% 20%, 30% 50%, 0% 80%, 20% 100%, 50% 70%, 80% 100%, 100% 80%, 70% 50%, 100% 20%, 80% 0%, 50% 30%);
    &.show {
      top: 25%;
    }
  }
  .zx-search {
    width: 50%;
    height: 50px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateX(-50%);
    transition: all .5s ease-in-out;
    &.show {
      top: 40%;
    }
    input {
      width: 100%;
      height: 100%;
      border: none;
      outline: none;
      // border-radius: 50px;
      padding: 15px 15px;
      // box-shadow: 0px 0px 10px 0px #ec1f2380;
      transition: all .5s ease-in-out;

      background: transparent;
      border-bottom: 1px solid #ffffff80;
      color: #fff;
      &::placeholder {
        font-family: reg;
        color: #fff;
        [dir="rtl"] & {
          font-family: ar-reg;
        }
      }
      &:hover, &:focus {
        border-bottom: 1px solid #f2dc91;
      }
    }
    button {
      font-size: 18px;
      color: #fff;
      width: 44px;
      height: 100%;
      background: transparent;
      // background-color: #ec1f23;
      position: absolute;
      top: 50%;
      left: 0px;
      transition: all .5s ease-in-out;
      transform: translateY(-50%);
      [dir="ltr"] & {
        left: unset;
        right: 0px;
      }
      &:hover {
        // background-color: #000;
        color: #f2dc91;
      }
    }
  }
}








