/*------ nav placeholder start ------*/
.nav-place {
  width: 100%;
  height: 100px;
  transition: all .3s ease-in-out;
  display: none;
  &.show {
    display: block;
  }
}
/*------ nav placeholder end ------*/


/*------ navbar start ------*/
.nav-bar {
  height: 100px;
  position: relative;
  background-color: #000;
  box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.07);
  z-index: 9;
  transition: all .5s ease-in-out;
  &.fixed-nav {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 999;
  }
  .zx-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 12px 0;
    height: 100%;
  }
  .logo-container {
    // width: 230px;
    .logo {
      width: 200px;
    }
  }
  .list-icons-container {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .list {
    display: flex;
    justify-content: center;
    align-items: center;
    >li {
      position: relative;
      font-family: reg;
      font-size: 16px;
      font-weight: 400;
      color: #f2dc91;
      text-transform: uppercase;
      letter-spacing: .1em;
      margin: 0 25px 0 0;
      transition: all .3s ease-in-out;
      [dir="rtl"] & {
        margin: 0 0 0 25px;
        font-family: bold;
      }
      &:last-child {
        margin: 0;
      }
      &:hover {
        color: #98918b;
        .sub-cat-hover {
          opacity: 1;
          visibility: visible;
          top: 30px;
        }
      }
      >a.active {
        color: #98918b;
      }
    }
    .sub-cat-hover {
      opacity: 0;
      visibility: hidden;
      position: absolute;
      top: 40px;
      left: -20px;
      [dir="rtl"] & {
        left: unset;
        right: -20px;
      }
      // transform: translate
      width: max-content;
      min-width: 190px;
      // min-height: 222px;
      background-color: rgba(0, 0, 0, 0.949);
      box-shadow: 0px 0px 6px 1px rgba(255, 255, 255, 0.6);
      // border-radius: 10px;
      padding: 7px 10px;
      display: flex;
      justify-content: flex-start;
      align-items: flex-start;
      flex-direction: column;
      z-index: 9;
      transition: all .5s ease-in-out;
      li {
        width: 100%;
        transition: all .3s ease-in-out;
        font-family: reg;
        font-size: 16px;
        color: #fff;
        border-bottom: 1px solid #ffffff33;
        padding: 7px 9px;
        margin: 0;
        // text-transform: none;
        [dir="rtl"] & {
          font-family: md;
        }
        &:last-child {
          border: none;
        }
        &:hover {
          color: #98918b;
        }
      }
    }
  }

  .nav-icons {
    .search-icon {
      margin: 0 0 0 35px;
      color: #fff;
      [dir="rtl"] & {
        margin: 0 35px 0 0;
      }
    }
  }
}


/*------ navbar end ------*/








/*------ header start ------*/
.header {
  // margin: 20px 0 0 0;
  transition: all .3s ease-in-out;
  .slide {
    position: relative;
    max-height: 900px;
    img {
      transition: all .6s ease-in-out;
    }
    .overlay {
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      background-color: rgba(0, 0, 0, 0.1);
      display: flex;
      justify-content: center;
      align-items: center;
    }
    .inner-slider-text {
      font-family: sec-bold;
      font-size: 80px;
      color: #ffffff;
      text-align: center;
      text-transform: uppercase;
      line-height: 1.2;
      max-width: 660px;
      >p {
        line-height: 1.1em;
        height: 200px;
      }
      // position: absolute;
      // top: 50%;
      // transform: translateY(-55%);
      // left: 130px;
      [dir="rtl"] & {
        font-family: ar-black;
        // left: unset;
        // right: 130px;
      }
    }
    .header-btn {
      font-family: reg;
      font-size: 13px;
      font-weight: 700;
      font-style: normal;
      text-transform: uppercase;
      letter-spacing: .28em;
      padding: 21px 34px;
      background-image: linear-gradient(90deg, #000, #000, #f2dc91, #f2dc91);
      background-size: 300%;
      transition: all .5s ease-in-out;
      &:hover {
        background-position-x: 100%;
        opacity: 0.9;
        color: #000;
      }
    }
    &:hover {
      img {
        transform: scale(1.016);
      }
    }
  }
    .owl-carousel {
      position: relative;
      .owl-nav {
        display: none;
      }
      .owl-dots {
        width: max-content;
        position: absolute;
        right: 60px;
        bottom: 50%;
        transform: translateY(-50%);
        [dir="rtl"] & {
          right: unset;
          left: 60px;
        }


        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        .owl-dot {
          width: 12px;
          height: 12px;
          background-color: #fff;
          outline: none;
          margin: 4px 0px;
          &.active {
            background-color: #f2dc91;
          }
        }
      }
    }
   
}

/*------ header end ------*/



/*------ Mission start ------*/
.mission {
  .mission-header {
    font-family: sec-bold;
    font-size: 80px;
    font-style: normal;
    letter-spacing: 0em;
    line-height: 1.1em;
    text-transform: none;
    text-align: center;
    max-width: 900px;
    margin: 90px auto 80px auto;
    cursor: default;
  }
  .trip-card {
    max-width: 380px;
    margin: 0 auto;
    .card-img {
      width: 100%;
      height: 300px;
      overflow: hidden;
      position: relative;
      img {
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        transition: all .9s ease-in-out;
        &:nth-of-type(2) {
          opacity: 0;
          z-index: 9;
        }
      }
      &:hover {
        img {
          transform: scale(1.05);
          &:nth-of-type(2) {
            opacity: 1;
          }
        }
      }
    }
    .card-body {
      .card-title {
        width: max-content;
        font-family: md;
        font-weight: 600;
        font-style: normal;
        font-size: 32px;
        letter-spacing: 0.03em;
        line-height: 1.2em;
        text-transform: none;
        padding: 0 0 5px 0;
        border-bottom-width: 1px;
        border-bottom-style: solid;
        border-bottom-color: rgba(0,0,0,.3);
        margin: 30px 0 20px 0;
        transition: all .3s ease-in-out;
        &:hover {
          // color: rgba(0,0,0,.6);
          color: #f2dc91;
          border-color: #f2dc91;
        }
      }
      .card-info {
        ul {
          margin: 15px 0;
          list-style-type: "-";
          li {
            font-family: light;
            font-weight: 400;
            font-style: normal;
            font-size: 20px;
            color: #000;
            letter-spacing: .03em;
            line-height: 1.3em;
            text-transform: none;
            padding: 0 3px;
            // margin: 0 0 6px 0;
          }
        }
      }
      .card-text {
        p {
          font-family: light;
          font-weight: 400;
          font-style: normal;
          font-size: 21px;
          color: #000;
          letter-spacing: .03em;
          line-height: 1.5em;
          text-transform: none;
          margin: 0 0 25px 0;
          a {
            border-bottom: 1px solid rgba(0,0,0,.3);
            transition: all .3s ease-in-out;
            &:hover {
              border-bottom: 1px solid rgba(0,0,0,.15);
              color: rgba(0,0,0,.6);
            }
          }
        }
      }
      .card-btn {
        display: block;
        width: max-content;
        border-width: 2px;
        border-style: solid;
        border-color: #000;
        border-radius: 50px;
        padding: 16px 26px;
        font-family: reg;
        font-size: 12px;
        color: #000;
        font-weight: 600;
        font-style: normal;
        text-transform: uppercase;
        letter-spacing: .15em;
        transition: all .5s ease-in-out;
        background-image: linear-gradient(90deg, #fff, #fff, #000, #000);
        background-size: 300%;
        &:hover {
          background-position-x: 100%;
          color: #fff;
        }
      }
    }
  }
  .owl-carousel {
    position: relative;
    .owl-nav {
      display: block;
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      left: 0;
      width: 100%;
      height: 0;
      .owl-prev, .owl-next {
        position: absolute;
        font-size: 24px;
        color: #000;
        transition: all .5s ease-in-out;
        &:hover {
          color: #f2dc91;
        }
      }
      .owl-prev {
        right: -36px;
      }
      .owl-next {
        left: -36px;
      }
    }
    .owl-dots {
      display: none !important;
      width: max-content;
      position: absolute;
      left: 50%;
      bottom: -50px;
      transform: translateX(-50%);

      display: flex;
      justify-content: center;
      align-items: center;
      .owl-dot {
        width: 12px;
        height: 12px;
        background-color: #00000033;
        outline: none;
        margin: 0px 5px;
        &.active {
          background-color: #f2dc91;
        }
      }
    }
  }
}
/*------ Mission end ------*/



