  .fixed-menu {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,0.5);
    z-index: 9999;
    .menu {
      // width: 260px;
      width: 100%;
      height: 100%;
      position: absolute;
      top: -100%;
      right: -280px;
      background-color: #000;
      padding: 30px 35px;
      overflow-y: auto;
      transition: all .5s ease-in-out;
      [dir="ltr"] & {
        top: -100%;
        right: unset;
        left: -280px;
      }
      &.show {
        top: 0;
        right: 0;
        [dir="ltr"] & {
          top: 0;
          right: unset;
          left: 0;
        }
      }
    }
    .menu-header {
      display: flex;
      justify-content: flex-start;
      align-items: center;
      .close {
        opacity: 1;
        color: #fff;
        font-size: 22px;
        position: absolute;
        top: 29px;
        left : 36px;
        [dir="ltr"] & {
          left: unset;
          right: 36px;
        }
      }
    }
    .main-list {
      text-align: center;
      // width: max-content;
      margin: 90px auto;
      opacity: 0;
      transform: translateY(50%);
      transition: all 1.1s ease-in-out;
      [dir="ltr"] & {
        transform: translateY(50%);
      }
      .slide {
        // cursor: pointer;
        >a {
          position: relative;
          &::after {
            content: "";
            font-family: "Linearicons-Free";
            font-size: 8px;
            color: #ffffff;
            // color: #0f0f0f;
            position: absolute;
            top: 5px;
            left: -15px;
            transition: all .5s ease-in-out;

            [dir="ltr"] & {
              left: unset;
              right: -15px;
            }
          }
          &.active {
            color: #ffffff;
            &::after {
              content: "";
              [dir="ltr"] & {
                content: "";
              }
            }
          }
        }
      }
      > li {
        font-family: md;
        font-size: 16px;
        color: #ffffff;
        text-transform: uppercase;
        margin: 15px 0;
        [dir="rtl"] & {
          font-family: ar-bold;
        }
        a {
          font-family: inherit;
          font-size: inherit;
          color: inherit;
        }
      }
      &.show {
        opacity: 1;
        transform: translateY(0%);
      }
    }
    .sub-list {
      padding: 5px 10px;
      display: none;
      .slide-two {
        cursor: pointer;
        position: relative;
        
        &::after {
          content: "";
          font-family: "Linearicons-Free";
          font-size: 8px;
          color: #ffffff;
          position: absolute;
          top: 9px;
          right: 15px;
          transition: all .5s ease-in-out;
          [dir="ltr"] & {
            right: unset;
            left: 15px;
          }
        }
        &.active {
          color: #ffffff;
          &::after {
            content: "";
            [dir="ltr"] & {
              content: "";
            }
          }
        }
      }
      li {
        font-family: reg;
        font-size: 16px;
        text-transform: none;
        color: #ffffff;
        margin: 12px 0;
        [dir="rtl"] & {
          font-family: ar-reg;
        }
        a {
          font-family: inherit;
          font-size: inherit;
          color: inherit;
        }
      }
    }
    .sub-list-copy {
      padding: 5px 15px;
      display: none;
      li {
        font-family: sec-reg;
        font-size: 15px;
        color: #ffffff;
        [dir="rtl"] & {
          font-family: ar-reg;
        }
        a {
          font-family: inherit;
          font-size: inherit;
          color: inherit;
        }
      }
    }
  }



